<template>
  <div class="list-box">
    <!-- <div :class="['list', { none: !list.length }]"> -->
    <transition-group :class="['list', { none: !list.length, hidden: !isShowAll }]" tag="div" name="todo-list">
      <div class="item grid" v-for="(item, inx) in list" :key="item.id">
        <div :class="['item-text', { finish: item.status == 2 }]" :title="item.value">{{ item.value }}</div>
        <dw-button class="item-btn" type="warning" v-if="item.status == 0" waiting @click="updateRow(item, 1)">执行中</dw-button>
        <dw-button class="item-btn" type="primary" v-if="item.status == 1" @click="updateRow(item, 0)">去执行</dw-button>
        <dw-button class="item-btn" type="success" v-if="item.status != 2" @click="updateRow(item, 2)">完成</dw-button>
        <dw-button class="item-btn" type="danger" @click="deleteRow(item)">删除</dw-button>
      </div>
    </transition-group>
    <!-- </div> -->
    <div>
      <dw-button type="info" :class="['pack-btn', { disabled: list.length <= 3 }]" @click="packUp()">{{ isShowAll ? "收起" : "展开" }}</dw-button>
    </div>
  </div>
</template>

<script>
export default {
  props: ["list"],
  data() {
    return {
      isShowAll: true,
    };
  },

  methods: {
    updateRow(item, status) {
      // this.isShowAll = false;
      this.$emit("update-row", item, status);
    },
    deleteRow(item) {
      this.$emit("delete-row", item);
    },
    packUp() {
      if (this.list.length <= 3) return;
      this.isShowAll = !this.isShowAll;
    },
  },
};
</script>

<style scoped>
.list-box {
  margin-top: 50px;
  width: 100%;
}
.list {
  width: 100%;
  height: 100%;
  /* 未生效？ */
  transition: transform 1s;
}
.none {
  border-top: 1px solid #d9d9d9;
  min-height: 50px !important;
}
.item {
  height: 50px;
  grid-template: 1fr/58% 16% 13% 13%;
  border-top: 1px solid #d9d9d9;
  padding: 0 10px;
  align-items: center;
}
.item-text {
  margin-right: 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.finish {
  text-decoration: line-through;
}

.item-btn {
  justify-self: center;
}
.pack-btn {
  width: 100%;
  border-radius: 0 0 4px 4px;
}
.disabled {
  cursor: no-drop;
  background: #ccc;
  border-color: #ccc;
}
.todo-list-move {
  transition: transform 1s;
}
.hidden {
  height: 150px;
  overflow: hidden;
}
</style>
