<template>
  <div class="box">
    <div class="title">TODO LIST</div>
    <input-btn @add="addNew"></input-btn>
    <todo-list :list="sortList" @update-row="updateRow" @delete-row="deleteRow"></todo-list>
  </div>
</template>

<script>
import inputBtn from "./components/inputBtn";
import todoList from "./components/todoList";

export default {
  name: "App",
  data() {
    return {
      list: [],
      sortList: [],
    };
  },
  components: {
    inputBtn,
    todoList,
  },
  watch: {
    list: {
      handler(nv) {
        if (nv.length <= 1) return (this.sortList = nv);
        // console.log(nv);
        // 拷贝防止排序的时影响到list
        const list = nv.map((i) => {
          return { ...i };
        });
        this.sortList = list.sort(this.mysort);
        // console.log(this.sortList);
      },
      deep: true,
    },
  },
  methods: {
    addNew(value, time) {
      this.list.push({
        id: time,
        value,
        status: 1, // 0在执行 1待执行 2已完成
        created: time,
        finished: null,
      });
    },
    // 根据状态排序
    mysort(a, b) {
      // 0在执行 1待执行 2已完成
      if (a.status != b.status) return a.status < b.status ? -1 : 1;
      // 已完成的根据完成时间
      else if (a.finished != b.finished) return a.finished > b.finished ? -1 : 1;
      // 待执行的根据创建时间
      else if (a.created != b.created) return a.created > b.created ? -1 : 1;
    },
    // 更新
    updateRow({ id }, status) {
      // 找出正在干活的
      const curDoing = this.list.find((item) => item.status == 0);
      // 如果是有要更改正在干活的。先修改原先的状态
      if (curDoing && status == 0) this.$set(curDoing, "status", 1);
      // 找出待更新的行
      const updateItem = this.list.find((item) => item.id == id);
      // 如果是完成，则增加完成时间
      if (status == 2) this.$set(updateItem, "finished", Date.now());
      console.log("我点的是", status ? (status == 1 ? "执行中" : "完成") : "去执行", updateItem.value);
      this.$set(updateItem, "status", status);
    },
    // 删除
    deleteRow({ id }) {
      this.list = this.list.filter((item) => item.id != id);
    },
  },
};
</script>

<style scoped>
.box {
  width: 600px;
  /* border: 1px solid #000; */
  /* margin-top: 20vh; */
  height: 50vh;
  color: #666;
}
.title {
  font-size: 40px;
  font-weight: 600;
  color: #666;
  text-align: center;
  height: 20vh;
  line-height: 20vh;
}
</style>
